import React, { Component} from 'react'




class RadioSelect extends Component {
  constructor(props) {
    super(props)
  }

  render() {
    return (
      <div>
        { this.props.label }
        { this.props.options.map((option, i) => (
          <label>
            { option.label }
            <input
              type="radio"
              value={ option.value }
              name={ option.name }
              checked={ this.props.defaultValue === option.value }
            />
          </label>
        ))}
      </div>
    )
  }

}


export default RadioSelect



